html {
  --main-color: black;
  --highlight-color: midnightblue;
  --hover-color: dodgerblue;
  --background-color: white;
}

header, main {
  width: 60%;
  min-width: 700px;
  max-width: 1000px;
  margin: auto;
}

.body__header {
  justify-content: space-between;
  margin-top: 2em;
  display: flex;
}

.header-title__employee-name {
  text-align: center;
  color: var(--highlight-color);
  margin-top: 0;
  margin-bottom: 0;
  position: relative;
}

.title-contacts, .header-title {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.contacts-links_margin-top {
  margin-top: .5em;
}

.title-contacts__contact {
  font-size: small;
}

.title-contacts__email, .title-contacts__divider {
  display: none;
}

.description__title {
  width: fit-content;
  width: -moz-fit-content;
  color: var(--highlight-color);
  background-color: var(--background-color);
  z-index: 2;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5%;
  position: relative;
}

.description__title-container {
  text-align: center;
  position: relative;
}

.description__title-container:after {
  content: "";
  width: 100%;
  border-top: 1px solid var(--highlight-color);
  z-index: 1;
  position: absolute;
  top: 50%;
  left: 0;
}

.description__summary-title {
  color: var(--highlight-color);
  cursor: pointer;
  margin-top: 1.3em;
}

.description__summary-title:hover {
  color: var(--hover-color);
}

a {
  color: var(--highlight-color);
  font-style: italic;
  text-decoration: none;
  transition: all .3s linear;
}

a:hover {
  color: var(--hover-color);
}

.history__dates {
  font-size: smaller;
}

.history__tech-stack, .history__faculty-title {
  margin-top: 0;
  margin-bottom: 0;
}

.history__title-without-margin {
  margin-bottom: 10px;
}

.contacts-links svg {
  width: 20px;
}

a:not(:hover) svg path {
  fill: var(--highlight-color);
  transition: all .6s linear;
}

a:hover svg path {
  transition: all .3s linear;
}

.contacts-links {
  justify-content: center;
  align-items: center;
  display: flex;
}

.contacts-link__link {
  margin-right: 10px;
}

.achivements__item {
  padding-bottom: 5px;
}

.aside-content__download-button {
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.aside-content__download-icon {
  width: 50px;
  height: 50px;
}

.aside-content__tooltip {
  background-color: var(--highlight-color);
  color: #fff;
  max-width: 250px;
  border-radius: 5px;
  padding: 5px;
  position: absolute;
}

.history__tech-stack span:not(:first-child) {
  cursor: pointer;
  -webkit-text-decoration: underline dashed;
  text-decoration: underline dashed;
}

.history__tech-stack span:not(:first-child):hover {
  color: var(--hover-color);
}

.language-button {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background: none;
  border: none;
  position: fixed;
  bottom: 90px;
  right: 30px;
}

@media screen and (max-width: 700px), print {
  header, main {
    width: 100%;
    min-width: 300px;
  }

  .body__header {
    flex-direction: column;
  }

  .description__columns {
    display: block;
  }

  .history__list {
    align-items: center;
    margin-top: 1.3em;
    display: flex;
  }

  .history__company-name {
    width: -moz-fit-content;
    border-right: 1px solid #191970;
    margin: 0;
    padding-right: 10px;
  }

  .history__dates {
    padding-left: 10px;
  }

  .history__title {
    margin-top: 10px;
  }
}

@media screen and (min-width: 701px) {
  .title-contacts {
    border-left: 1px solid #d3d3d3;
    padding-left: 10px;
  }

  .description__item {
    grid-column-gap: 20px;
    grid-template-columns: 250px 1fr;
    display: grid;
  }

  .history__list {
    text-align: center;
  }

  .history__company-name {
    border: 1px solid #191970;
    border-radius: 5px;
    margin-bottom: .5em;
    padding: 15px;
  }
}

@media (prefers-color-scheme: dark) {
  html {
    --highlight-color: dodgerblue;
    --hover-color: deepskyblue;
    --background-color: black;
    --main-color: white;
    background-color: var(--background-color);
    color: var(--main-color);
  }
}

@media print {
  html {
    --highlight-color: black;
    --hover-color: black;
    --background-color: white;
    --main-color: black;
  }

  .body__header {
    margin-top: 0;
  }

  .body__aside-content, .contacts-links_not-printed, .description__details {
    display: none;
  }

  .title-contacts {
    flex-direction: row;
    gap: 10px;
  }

  .title-contacts__email, .title-contacts__divider {
    display: block;
  }
}

